import './style.css';
import helpBG from '../../images/form-bg.svg';
import { useState } from 'react';

export default function Form() {
  const [value, setValue] = useState({
    name: '',
    contacts: ''
  });
  function submitHandler(evt) {
    evt.preventDefault();
  }

  return(
    <section className='form'>
      <div className='auto-width'>
        <div className='form__content auto-padding' data-aos='fade-right'>
          <form className='form__main-form'>
            <img className='form__help-bg' src={helpBG} alt="" />
            <div className='form__info-block'>
              <p className='form__title'>
                <br />   
              </p>
              <div className='form__luring'>
                 ,    
              </div>
            </div>
            <div className='form__input-block'>
              <p className='form__subtext'>
                 ,         
              </p>
              <input onChange={(evt) => setValue({...value, name: evt.target.value})} placeholder='' className='form__input' type="text" name="name" value={value.name} />
              <input onChange={(evt) => setValue({...value, contacts: evt.target.value})} placeholder=', , ' className='form__input' type="text" name="contacts" value={value.contacts} />
              <button onClick={submitHandler} className='form__submit' type="submit">
                
              </button>
              <p className='form__confident'>
                  ,   <a href='https://ya.ru' target='_blank' className='form__confident-link'>   </a>
              </p>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
}